<template>
	<div>
		<template v-if="loadingComment">
			<div class="loading">
				<Loader />
			</div>
		</template>
		<template v-else>
			<div class="main__header">
				<div class="main__primary main__primary--block">
					<p>Comment body:</p>
					<h3 class="main__title bold">{{ comment.body }}
					</h3>
					<button @click="deleteComment" class="btn btn__danger">Delete</button>
				</div>
			</div>
		</template>
	</div>
</template>

<script>
import { getForumThreadItem, deleteThreadItem } from '../../utils/api/forum';
import Loader from '../../components/Loader';
import { success, fail } from '../../utils/toast';

export default {
	name: 'ForumComment',
	data() {
		return {
			comment: '',
			loadingComment: true,
			commentLoaded: false
		};
	},
	created() {
		this.getComment();
	},
	methods: {
		getComment() {
			getForumThreadItem(this.$route.params.forumId, this.$route.params.threadId)
				.then((response) => {
					if (response.status === 200) {
						this.commentLoaded = true;
						this.comment = response.data.data;
						return true;
					}
					this.commentLoaded = false;
					return false;
				})
				.catch(() => {
					this.loadingComment = false;
					return false;
				})
				.finally(() => {
					this.loadingComment = false;
				});
			return true;
		},
		deleteComment() {
			this.loading = true;
			deleteThreadItem(this.$route.params.forumId, this.$route.params.threadId)
				.then((response) => {
					if (response.status === 200) {
						this.loading = false;
						this.$router.push({
							name: 'forum-thread',
							params: { id: this.$route.params.forumId } 
						});
						success({
							title: 'Success! Forum has been deleted'
						});
					}
					return false;
				})
				.catch((error) => {
					this.loading = false;
					fail({
						title: 'Oops! Something went wrong. Please try again.'
					});
					return error;
				})
				.finally(() => {
					this.loading = false;
				});

			return true;
		}
	},
	components: {
		Loader
	}
};
</script>

<style lang="scss" scoped>
.loading {
	min-height: 80vh;
	display: flex;
	justify-content: center;
	align-items: center;
}

.btn__danger {
    background-color: #fff;
    color: #DD4B39;
    border-color: #DD4B39;
}
.main__primary {
	&--block {
		display: block;
	}
}
</style>
